.app {
  font-family: 'Ropboto', sans-serif;
  line-height: 1.5;
  color: var(--clr-fg);
  background-color: var(--clr-bg);
}

.light {
  --clr-bg: #fcfcfc;
  --clr-bg-alt: #fff;
  --clr-fg: #555;
  --clr-fg-alt: #444;
  --clr-primary: #2978b5;
  --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    color: #0b0b15;
}

.dark {
  --clr-bg: #23283e;
  --clr-bg-alt: #2a2f4c;
  --clr-fg: #bdbddd;
  --clr-fg-alt: #cdcdff;
  --clr-primary: #90a0d9;
  --shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  color: rgb(255 255 255 / 87%);
}

.dark .MuiTypography-body2 {
    color: #e4e8ee;
}

.light .MuiTypography-body2 {
    color: #04061a;
}

main {
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
}

.section {
  margin-top: 5em;
}

.section__title {
  text-align: center;
  margin-bottom: 1em;
  text-transform: uppercase;
}

.center {
  display: flex;
  align-items: center;
}

.link {
  color: var(--clr-primary);
  padding: 0 0 0.3em 0;
  position: relative;
}

.link:hover {
  color: var(--clr-primary);
}

.link::before {
  content: '';
  display: inline;
  width: 0%;
  height: 0.2em;
  position: absolute;
  bottom: 0;
  background-color: var(--clr-primary);
  transition: width 0.2s ease-in;
}

.link:hover::before,
.link:focus::before {
  width: 100%;
}

.link--nav {
  color: var(--clr-fg);
  text-transform: lowercase;
  font-weight: 500;
}

.link--icon {
  color: var(--clr-fg);
}

.btn {
  display: block;
  cursor: pointer;
  padding: 0.8em 1.4em;
  font-weight: 500;
  font-size: 0.9rem;
  text-transform: lowercase;
  transition: transform 0.2s ease-in-out;
}

.btn--outline {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn--outline:hover,
.btn--outline:focus {
  color: var(--clr-bg);
}

.btn--outline:before {
  content: '';
  position: absolute;
  background-color: var(--clr-primary);
  right: 100%;
  bottom: 0;
  left: 0;
  top: 0;
  z-index: -1;
  transition: right 0.2s ease-in-out;
}

.btn--outline:hover:before,
.btn--outline:focus:before {
  right: 0;
}

.btn--plain {
  text-transform: initial;
  background-color: var(--clr-bg-alt);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
  border: 0;
}

.btn--plain:hover {
  transform: translateY(-4px);
}

.btn--icon {
  padding: 0;
}

.btn--icon:hover,
.btn--icon:focus {
  color: var(--clr-primary);
}

.btn--icon:active {
  transform: translateY(-5px);
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

.typing {
  white-space: pre;
  overflow: hidden;
  border-right: .15em solid;
}

.typing::after {
  content: '|'; /* Cursor */
  animation: blink 1s infinite;
}

@keyframes blink {
  from, to { border-color: transparent }
  50% { border-color: currentColor }
}

.welcome-message-text-motion {
    font-size: 40px;
    font-weight: bold;
}

@media (max-width: 768px) {
  .section {
    margin-top: 2em; /* Reduce top margin */
  }
  .container {
    padding: 2em; /* Reduce container padding */
  }
  h3 {
    font-size: 1.5rem; /* Reduce title font size */
  }
  .btn {
    font-size: 0.8rem; /* Reduce button font size */
    padding: 0.6em 1em; /* Reduce button padding */
  }
  .welcome-message-text, .welcome-message-text-motion {
        font-size: 20px;
        margin-bottom: 1.5rem;
  }
  .loading-text-message {
    font-size: 24px;
    }
  .loading-text-box {
    font-size: 24px;
  }
}

.form-container {
    padding: 2rem;
    border-radius: 8px;
    transition: background-color 0.3s;
}

.form-motion-div {
    width: 100%;
    max-width: 600px;
    padding: 2rem;
    border-radius: 8px;
    transition: background-color 0.3s, box-shadow 0.3s;
}

.loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
}

.recommendation-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.form-inner-container {
    padding: 2rem;
    border-radius: 8px;
    transition: background-color 0.3s;
}

/* Light Theme Styling */

.light .form-container {
    background-color: #f7f7f7;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.08);
}

.light .form-motion-div {
    background-color: #ffffff;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.08);
}

.light .form-inner-container {
    background-color: #ffffff;
}

/* Dark Theme Styling */

.dark .form-container {
    background-color: #0b0b15;
    box-shadow: 0px 4px 16px rgba(255, 255, 255, 0.08);
}

.dark .form-motion-div {
    background-color: #222;
    box-shadow: 0px 4px 16px rgba(255, 255, 255, 0.08);
}

.dark .form-inner-container {
    background-color: #222;
}

.dark .loading-container {
    color: #fff;
}
